@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 2.625em;
    font-weight: 500;
    border-radius: 6.25em;
    &:focus {
        box-shadow: none;
    }
    &:disabled{
        pointer-events: none;
    }
    span{
        font-size: 1.125em;
        line-height: 144%;
    }
    img,
    svg{
        width: 1.5em;
        height: auto;
        margin-left: .5em;
    }
    &._round{
        width: 2.25em;
        height: 2.25em;
        padding: 0;
        border-radius: 50%;
        svg{
            margin: 0;
            width: 1.25em;
        }
    }
}

.m-btn-primary,
.m-btn-secondary{
    &:disabled{
        background-color: $grey20;
        color: $grey40;
        path{
            fill: $grey40;
        }
    }
}


.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: $primaryHover;
        color: #fff;
    }
    &:active{
        background-color: $primaryPressed;
        color: #fff;
    }
    &:focus{
        box-shadow: 0 0 0 3px $primaryFocus;
        color: #fff;
    }
}


.m-btn-secondary{
    background-color: $primarySecondary;
    color: $primary;
    &:hover{
        background-color: $primarySecondary;
        color: $primaryHover;
        path{
            fill: $primaryHover;
        }
        ._stroke{
            fill: none;
            stroke: $primaryHover;
        }
    }
    &:active{
        background-color: $primarySecondary;
        color: $primaryPressed;
        path{
            fill: $primaryPressed;
        }
        ._stroke{
            fill: none;
            stroke: $primaryPressed;
        }
    }
    &:focus{
        box-shadow: 0 0 0 3px $primaryFocus;
        color: $primary;
    }
}


.m-btn-text{
    color: $primary;
    padding: 0 1em;
    &:hover{
        color: $primaryHover;
        path{
            fill: $primaryHover;
        }
        ._stroke{
            fill: none;
            stroke: $primaryHover;
        }
    }
    &:active{
        color: $primaryPressed;
        path{
            fill: $primaryPressed;
        }
        ._stroke{
            fill: none;
            stroke: $primaryPressed;
        }
    }
    &:focus{
        color: $primary;
    }
    &:disabled{
        color: $grey40;
    }
}


.m-btn-primary-outline{
    border: 1px solid $primaryBorder;
    color: $primary;
    &._gray{
        border-color: $grey40;
    }
    &:hover{
        border-color: $primaryHover;
        color: $primaryHover;
        path{
            fill: $primaryHover;
        }
        ._stroke{
            fill: none;
            stroke: $primaryHover;
        }
    }
    &:active{
        border-color: $primaryPressed;
        color: $primaryPressed;
        path{
            fill: $primaryPressed;
        }
        ._stroke{
            fill: none;
            stroke: $primaryPressed;
        }
    }
    &:focus{
        border-color: $primaryFocus;
        box-shadow: 0 0 0 2px $primaryFocus;
        color: $primary;
    }
    &:disabled{
        color: $grey40;
        border-color: $grey40;
        box-shadow: 0 0 0 2px $grey40;
        path{
            fill: $grey40;
        }
        ._stroke{
            fill: none;
            stroke: $grey40;
        }
    }
}


.header-user-btn,
.burger,
.close-btn{
    width: 2.625em;
    @include center;
    img{
        width: 100%;
    }
}

.m-btn-line{
    padding: 0 1em;
    border-bottom: 2px solid transparent;
    border-top: 2px solid transparent;
    color: $grey90;
    border-radius: 0;
    &:hover{
        color: $grey90;
        border-bottom-color: $primaryHover;
        ._fill{
            fill: $primaryHover
        }
        ._stroke{
            stroke: $primaryHover;
        }
    }
    &:focus{
        box-shadow: 0 0 0 3px $primaryFocus;
        border-color: transparent;
        ._fill{
            fill: $primary
        }
        ._stroke{
            stroke: $primary
        }
    }
    &:active{
        border-color: transparent;
        box-shadow: none;
        ._fill{
            fill: $primaryPressed;
        }
        ._stroke{
            stroke: $primaryPressed;
        }
    }
}


.badge{
    background-color: $grey20;
    padding: 0 .75em;
    height: 1.25em;
    border-radius: 6.25em;
    color: $grey100;
    @include center;
    &:hover{
        color: $primaryHover;
        ._fill{
            fill: $primaryHover
        }
        ._stroke{
            stroke: $primaryHover;
        }
    }
    &:focus{
        color: $primaryFocus;
        box-shadow: 0 0 0 .125em $primaryFocus;
    }
    &._disabled,
    &:disabled{
        color: $grey40;
        box-shadow: 0 0 0 .125em $grey40;
        ._fill{
            fill: $grey40
        }
        ._stroke{
            stroke: $grey40;
        }
    }
    img,
    svg{
        width: .75em;
        height: auto;
        margin-left: .5em;
        &:first-child{
            margin: 0 .5em 0 0;
        }
    }
    &__remove{
        img,
        svg{
            margin: 0 0 0 .5em !important;
        }
    }
    &._big{
        height: 1.75em;
        font-weight: 500;
        img,
        svg{
            width: 1em;
        }
    }
}

.tag{
    border: 1px solid $grey100;
    padding: .5em 1.5em;
    min-height: 2.75em;
    height: unset;
    color: $grey100;
    @include center;
    &:hover{
        border-color: $primaryHover;
        color: $primaryHover;
        ._fill{
            fill: $primaryHover
        }
        ._stroke{
            stroke: $primaryHover;
        }
    }
    &:active{
        border-color: $primaryPressed;
        color: $primaryPressed;
        ._fill{
            fill: $primaryPressed
        }
        ._stroke{
            stroke: $primaryPressed;
        }
    }
    &:focus{
        color: $primary;
        border-color: $primaryFocus;
        box-shadow: 0 0 0 2px $primaryFocus;
    }
    &._disabled,
    &:disabled{
        background-color: $grey20;
        color: $grey50;
        border-color: $grey50;
        box-shadow: 0 0 0 2px $grey50;
        ._fill{
            fill: $grey50
        }
        ._stroke{
            stroke: $grey50;
        }
    }
    svg{
        width: .75em;
        height: auto;
        margin-left: .5em;
    }
    &__remove{

    }
}
