.courses{
  padding: 1.5em 0 3.5em;
  &__inner{
    margin-top: 3em;
    display: grid;
    grid-template-columns: 17.5em calc(100% - 18.75em);
    gap: 1.25em;
  }
  &__sidebar{
    padding-right: 1.25em;
    border-right: 2px solid $grey20;
  }
  &__checks{
    @include grid100gap(.75em);
  }
  .global-search{
    max-width: 100%;
  }
  &__items{
    margin-top: .75em;
  }
  &__more{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__selected-filters{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1em;
    .badge{
      margin: 1em 1em 0 0;
    }
  }
  &__clear-filter{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
}

.filter{
  &__top,
  &__nav{
    display: none;
  }
}

.filter-info{
  display: none;
}

.filter-group{
  @include lastM(0 0 3em);
  &__top{
    margin-bottom: .75em;
  }
  &__checks{
    @include grid100gap(.75em);
  }
  &__more{
    padding: 0;
    margin-top: .81em;
    ._view2{
      display: none;
    }
  }
  .local-search{
    margin-bottom: .75em;
  }
  &__bottom{
    @media screen and (min-width: 993px){
      display: block !important;
    }
  }
}

.sort-fields{
  margin-bottom: 3em;
}

._hide-checks{
  @media screen and (min-width: 993px){
    &:not(._show-all) .m-check:nth-child(5)~.m-check{
      display: none;
    }
    &._show-all~.filter-group__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.local-search{
  position: relative;
  z-index: 3;
  &__field{
    position: relative;
    z-index: 3;
    input{
      height: 2em;
      padding-left: 1.6em;
      padding-right: 1.6em;
    }
  }
  &__icon{
    left: .75em;
    width: 1em;
  }
  &__clear{
    width: 1em;
    right: .75em;
  }
  &__results{
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1.5em;
    padding-top: 2.5em;
    width: 100%;
    background-color: #fff;
    border: 1px solid $grey40;
    z-index: 2;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__item{
    padding: .38em 1em;
    font-weight: 500;
    color: $grey90;
    &:not(:last-child){
      border-bottom: 1px solid $grey20;
    }
  }
  &._show-results{
    .local-search__clear{
      opacity: 1;
      pointer-events: unset;
    }
    .local-search__field{
      input{
        background-color: transparent;
        border: none;
      }
    }
    .local-search__results{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.sub-filter{
  &__top{
    display: none;
  }
}

.filter-slider{
  margin-top: .75em;
  &__item{
    padding: 0 .625em 0 .35em;
    min-height: 1.125em;
    display: flex;
    align-items: center;
    position: relative;
    &:after,
    &:before{
      content: '';
      position: absolute;
      width: 0.375em;
      height: 0.375em;
      border-radius: 50%;
      background-color: $grey40;
      top: 50%;
      transform: translateY(-50%);
    }
    &:before{
      left: 0;
    }
    &:after{
      right: 0;
    }
  }
  .range-slider{
    width: 100%;
    background-color: #E0E0E0;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: 2px solid $primary;
      background-color: $primary;
      width: 1.125em;
      height: 1.125em;
      top: -.5em !important;
      right: -.5em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin-top: .25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.specialization{
  margin-top: 2em;
  border-radius: 1.5em;
  background-color: $grey20;
  padding: 1.5em;
  &__links{
    margin-top: 1.25em;
    display: flex;
    flex-wrap: wrap;
    .tag{
      margin: .75em .75em 0 0;
    }
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    ._view2{
      display: none;
    }
  }
  &:not(._show-all) .tag:nth-child(8)~.tag{
    display: none;
  }
  &._show-all .specialization__btn{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
    svg{
      transform: rotate(-180deg);
    }
  }
}

.filter-mob{
  display: none;
}

.sort-field{
  margin-top: 2em;
  display: inline-block;
}

.empty-search{
  display: flex;
  align-items: center;
  padding: .75em 0;
  margin-top: 2em;
  &__img{
    width: 5em;
    flex-shrink: 0;
    margin-right: 2.62em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.popular-courses2{
  margin-top: 7em;
  &__items{
    margin-top: 2em;
    row-gap: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .courses{
    padding-bottom: 1.5em;
    &__inner{
      gap: 2.25em;
      margin-top: 1.5em;
      grid-template-columns: 100%;
      padding-top: 5.5em;
      position: relative;
    }
    &__sidebar{
      border: none;
      padding: 0;
    }
    .global-search{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
    .sort-field{
      display: none;
    }
  }

  ._show-filter{
    .filter{
      transform: translateX(0);
    }
  }

  .filter{
    z-index: 20;
    @include menu;
    //transform: translateX(0);
    &._remove-scroll{
      overflow: hidden;
      height: $vh;
      .filter__content{
        height: 0;
        overflow: hidden;
      }
    }
    &__top{
      .close-btn{
        margin-left: auto;
      }
    }
    &__nav{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .filter-info{
    @include grid100gap(1.5em);
    padding: 1.5em 1em;
    &__top{
      display: flex;
      align-items: center;
      .badge{
        margin-left: .75em;
      }
    }
    &__selected{
      display: flex;
      flex-wrap: wrap;
      margin-top: -1em;
      .badge{
        margin: 1em 1em 0 0;
      }
    }
  }

  .sub-filter{
    z-index: 10;
    @include menu;
    //transform: translateX(0);
    &__top{
      .close-btn{
        margin-left: auto;
      }
    }
    &__content{
      padding: 1.5em 1em;
    }
    &__back{
      padding: 0 1em;
      height: 2.75em;
      svg{
        width: 1em;
        margin: 0 .25em 0 0;
      }
    }
  }

  .filter-group{
    @include lastM(0 0 1.5em);
    &._show-sub-filter{
      .sub-filter{
        transform: translateX(0);
      }
    }
    &__more{
      display: none;
    }
    &__top{
      border-bottom: 1px solid $primary;
      padding: .5em 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      text-transform: unset;
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/arrow-down.svg);
        @include bgImgSize(100%);
        transition: all .4s;
      }
    }
    &__bottom{
      display: none;
    }
  }

  .specialization{
    display: none;
  }

  .filter-mob{
    display: flex;
    &__btn{
      padding: 0 .94em;
      margin-left: .5em;
      height: 2.75em;
      img{
        margin: 0;
      }
    }
    .m-select{
      flex-grow: 1;
    }
  }

  .sort-fields{
    margin: 1em 0 0;
    display: flex;
    gap: unset;
    flex-wrap: wrap;
    .m-check{
      margin: 1em 2em 0 0;
    }
  }

  .empty-search{
    margin-top: 0;
    align-items: flex-start;
    &__img{
      margin-right: 1em;
    }
  }

  .popular-courses2{
    margin-top: 7.5em;
    &__items{
      row-gap: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .courses{
    &__selected-filters{
      flex-direction: column;
      align-items: center;
      .badge{
        margin: 1.5em 1em 0;
      }
    }
    &__clear-filter{
      .m-btn{
        width: 100%;
      }
    }
  }
}
