.ui-elements{
  @include grid100gap(1em);
  padding: 2em;
}

.ui-buttons{
  display: flex;
  gap: 1em;
}

.m-select__toggle,
.fg .nice-select{
  position: relative;
  z-index: 3;
  padding: 0 .75em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 1.5em;
  border: 1px solid $grey40;
  height: 2.75em;
  width: 100%;
  &:before{
    order: 3;
    content: '';
    flex-shrink: 0;
    width: 1em;
    height: 1em;
    background-image: url(../img/arrow-down.svg);
    @include bgImgSize(100%);
    transition: all .4s;
    margin-left: .25em;
  }
  &:hover{
    border-color: $primary;
  }
  &:focus {
    border-color: $primaryFocus;
    box-shadow: 0 0 0 2px $primaryFocus;
  }
}

.fg .list,
.m-select__dropdown{
  top: 0;
  margin: 0;
  width: 100%;
  border-radius: 1.5em;
  padding-top: 2.75em;
  background-color: #fff;
  border: 1px solid $grey40;
  z-index: 2;
}

.fg{
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .75em;
    z-index: 5;
    width: 1em;
    &~.nice-select{
      padding-left: 1.78em;
    }
  }
  .nice-select{
    font-size: 1em;
  }
  .nice-select.open{
    border: none;
    box-shadow: none;
    .current{
      position: relative;
      z-index: 3;
    }
    &:before{
      position: relative;
      z-index: 3;
      transform: rotate(-180deg);
    }
  }
  .list{
    width: auto;
    min-width: 100%;
  }
  .option{
    display: flex;
    padding: .33em .88em;
    font-weight: 500;
    border: none !important;
    font-size: 1.125em;
    min-height: 1.9em;
    &.selected{
      background-color: $primarySecondary !important;
      color: $primary !important;
      font-weight: 500;
    }
  }
}

.m-select{
  position: relative;
  z-index: 2;
  &__dropdown{
    position: absolute;
    left: 0;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__option{
    display: flex;
    padding: .38em 1em;
    font-weight: 500;
  }
  &._open{
    .m-select__toggle{
      border: none;
      box-shadow: none;
      &:after{
        transform: rotate(-180deg);
      }
    }
    .m-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}
