.category-card,
.main{
  &__tags{
    margin-top: .75em;
    width: calc(100% + .75em);
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .m-btn,
    .tag{
      margin: .75em .75em 0 0;
    }
  }
}

.main{
  padding: 4em 0 3.75em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    width: 23.75em;
    height: 23.75em;
    background-color: $grey20;
    border-radius: 50%;
    z-index: -1;
    left: -7.5em;
    bottom: -.625em;
  }
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 22.3em) 22.3em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    max-width: 52.3em;
    margin-top: -10em;
  }
  &__tags{

  }
}

.global-search,
.local-search{
  &__icon,
  &__clear{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  &__clear{
    opacity: 0;
    pointer-events: none;
    right: 1em;
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      ._fill{
        fill: $primaryHover
      }
      ._stroke{
        stroke: $primaryHover;
      }
    }
    &:active{
      ._fill{
        fill: $primaryPressed;
      }
      ._stroke{
        stroke: $primaryPressed;
      }
    }
  }
}

.global-search{
  z-index: 3;
  width: 100%;
  max-width: 41.25em;
  position: relative;
  &__field{
    position: relative;
    z-index: 3;
    input{
      font-size: 1.25em;
      width: 100%;
      height: 2.8em;
      border-radius: 5em;
      padding: 0 2.8em;
      border: 2px solid $primary;
      &:focus{
        border-color: $primaryFocus;
        box-shadow: 0 0 0 2px $primaryFocus;
      }
      &::placeholder{
        color: $grey60;
      }
    }
  }
  &__icon{
    left: 1em;
    width: 2em;
  }
  &__results{
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    padding: 2.5em 1em 1.5em;
    box-shadow: $shadow;
    background-color: #fff;
    border-radius: 1.5em;
    z-index: 2;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &._show-results{
    .global-search__field{
      input{
        padding-left: .8em;
      }
    }
    .global-search__icon{
      opacity: 0;
      pointer-events: none;
    }
    .global-search__results,
    .global-search__clear{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.search-group{
  @include lastM(0 0 1.5em);
  &__top{
    margin-bottom: .75em;
  }
  &__last{
    margin-top: -.75em;
    display: flex;
    flex-wrap: wrap;
    .badge{
      margin: .75em .75em 0 0;
    }
  }
  &__items{
    @include grid100gap(.75em);
  }
}

.search-card{
  border-radius: 1.5em;
  display: grid;
  grid-template-columns: 6.25em calc(100% - 6.25em);
  background-color: $grey20;
  &__img{
    border-radius: 1.5em 0 0 1.5em;
    @include imgCover(100%);
  }
  &__content{
    padding: .5em;
  }
  &__title{
    @include truncate(2);
  }
  &__props{
    margin-top: .5em;
    display: flex;
    color: $grey100;
  }
  &__prop{
    display: flex;
    align-items: center;
    @include lastM(0 2em 0 0);
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

._section{
  padding: 3.75em 0;
}

.categories{
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__group{
    @media screen and (min-width: 993px){
      grid-row: 1/4;
      .category-card{
        height: 100%;
        display: block;
        &__img{
          width: 15.0625em;
          right: 0;
        }
      }
    }
  }
}

.category-card{
  min-height: 10em;
  padding: 2em;
  border-radius: 1.5em;
  background-color: $grey20;
  display: flex;
  align-items: flex-end;
  position: relative;
  &:hover{
    color: $black;
    background-color: $primarySecondary;
    .category-card__title{
      color: $primaryHover;
      svg{
        transform: rotate(-45deg);
      }
    }
    ._fill{
      fill: $primaryHover
    }
    ._stroke{
      stroke: $primaryHover;
    }
  }
  &:focus{
    color: $black;
    box-shadow: 0 0 0 3px $primaryFocus;
    .category-card__title{
      color: $black;
    }
    ._fill{
      fill: $primary
    }
    ._stroke{
      stroke: $primary;
    }
  }
  &._disabled{
    pointer-events: none;
    .category-card__title{
      color: $grey40;
      ._fill{
        fill: $grey40
      }
      ._stroke{
        stroke: $grey40;
      }
    }
  }
  &__title{
    transition: all .3s;
    display: flex;
    align-items: center;
    svg{
      margin-left: .75em;
      width: 1.5em;
      height: auto;
    }
  }
  &__img{
    position: absolute;
    width: 10em;
    height: 100%;
    right: 1em;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__mob-link{
    display: none;
  }
}

.banner{
  &__item{
     img{
       display: block;
       width: 100%;
       border-radius: 1.5em;
     }
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 2.5em);
    }
    &-slide{
      padding-right: 2.5em;
    }
  }
}

.banner-slider{
  .slider__wrap{
    border-radius: 2.5em;
  }
  .swiper-pagination{
    display: none;
  }
}

.popular-courses{
  &__items{
    margin-top: 3em;
  }
}

.course-card{
  display: block;
  border-radius: 1.5em;
  padding: 1.5em;
  border: 1px solid $primaryBorder;
  background: $bg;
  &:hover{
    color: $black;
    outline: 1px solid $primaryHover;
    box-shadow: 0 .25em .5em 0 rgba(170, 220, 243, 0.10), 0 0 0 1px $primaryHover;
  }
  &:focus{
    border-color: $primaryFocus;
    box-shadow: 0 .25em .5em 0 rgba(170, 220, 243, 0.10), 0 0 0 2px $primaryFocus;
  }
  &__top{
    display: flex;
  }
  &__img{
    width: 6.25em;
    flex-shrink: 0;
    margin-right: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__badges{
    display: flex;
    flex-wrap: wrap;
    max-width: 16.75em;
    margin-top: -.75em;
    .badge{
      margin: .75em .75em 0 0;
    }
  }
  &__content{
    margin-top: 3em;
  }
  &__code{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__title{
    @include truncate(3);
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1.5em 0;
    &:before{
      display: none;
    }
    &__top{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__text{
      text-align: center;
    }
    &__img{
      max-width: 30em;
      width: 100%;
      padding: 0 2.94em;
      margin: 0 auto;
    }
    &__bottom{
      margin-top: 1.5em;
    }
    &__tags{
      margin-top: .5em;
      .m-btn,
      .tag{
        margin-top: 1em;
      }
    }
  }

  .global-search{
    max-width: 100%;
  }

  .search-card{
    grid-template-columns: 100%;
    &__img{
      display: none;
    }
    &__props{
      display: block;
    }
    &__prop{
      @include lastM(0 0 .25em);
    }
  }

  ._section{
    padding: 1.5em 0;
  }

  .categories{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .category-card{
    flex-direction: column;
    align-items: center;
    &__mob-link{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      display: block;
    }
    &__tags{
      display: none;
    }
    &__img{
      order: -1;
      position: static;
      height: 7.5em;
      width: auto;
      margin-bottom: 4em;
      img{
        height: 100%;
        width: auto;
      }
    }
  }

  .banner{
    padding: 2.5em 0;
    .container{
      padding: 0;
    }
  }

  .banner-slider{
    .swiper{
      &-button{
        position: static;
        transform: unset;
        &-next{
          order: 2;
        }
      }
      &-pagination{
        margin: 0 .67em;
        display: block;
      }
    }
    &__nav{
      margin-top: .75em;
      @include center;
    }
  }

  .popular-courses{
    &__items{
      margin-top: 1.5em;
    }
  }

  .course-card{
    &__img{
      width: 3.75em;
    }
    &__content{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__tags{
      .m-btn-primary{
        width: 100%;
        margin-top: 1.5em;
      }
    }
  }

  .categories{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
