@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_grid.scss";
.swiper-button{
  width: 3em;
  height: auto;
  @include center;
  margin: 0;
  top: 50%;
  transform: translateY(-50%);
  svg{
    width: 100%;
    height: auto;
  }
  &-prev{
    left: -4.38em;
  }
  &-next{
    right: -4.38em;
  }
  &:hover{
    ._fill{
      fill: $primaryHover
    }
    ._stroke{
      stroke: $primaryHover;
    }
  }
  &:active{
    ._fill{
      fill: $primaryPressed
    }
    ._stroke{
      stroke: $primaryPressed;
    }
  }
  &.swiper-button-disabled{
    ._fill{
      fill: $grey50
    }
    ._stroke{
      stroke: $grey50;
    }
  }
}

.swiper-pagination{
  position: static;
  width: auto;
  font-weight: 500;
  font-size: 1.125em;
  line-height: 145%;
  color: $grey60;
  &-current{
    color: $grey100;
  }
}
