.login{
  padding: 2em 0 3.5em;
  &__inner{
    display: flex;
    justify-content: space-between;
    padding-right: 6.25em;
  }
  &__img{
    width: 35em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}


.login-form{
  width: 22.5em;
  max-width: 100%;
  &__title{
    text-align: center;
  }
  &__fields{
    margin-top: 2.5em;
    @include grid100gap(2em);
  }
  &__btn{
    margin-top: 3em;
    width: 100%;
  }
  &__links{
    font-weight: 500;
    margin-top: 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.login-alternative{
  margin-top: 4em;
  @include grid100gap(.75em);
  &__links{
    display: flex;
    justify-content: space-between;
  }
  ._red{
    border-color: $secondaryBorder;
    color: $secondaryMain;
    &:hover{
      background-color: rgba($secondaryMain, .1);
    }
  }
  ._yellow{
    border-color: $warningBorder;
    color: $warningMain;
    &:hover{
      background-color: rgba($warningMain, .1);
    }
  }
  img{
    width: 2em;
    margin-right: .75em;
  }
}


@media screen and (max-width: 992px){
  .login{
    padding: 1.5em 0;
    &__inner{
      display: block;
      padding: 0;
    }
    &__img{
      display: none;
    }
  }

  .login-form{
    width: 100%;
  }
}
