.fg {
  position: relative;
  margin: 0;
  label{
    font-size: 1.125em;
    line-height: 145%;
    color: $grey90;
    font-weight: 500;
    display: block;
    margin-bottom: .22em;
  }
  input {
    border: 1px solid $grey40;
    padding: 0 .6em;
    height: 2.2em;
    width: 100%;
    font-size: 1.25em;
    border-radius: 1.2em;
    background-color: transparent;
    color: $grey100;
    &::placeholder {
      color: $grey70;
    }
    &:hover{
      border-color: $primary;
    }
    &:focus {
      border-color: $primaryFocus;
      box-shadow: 0 0 0 2px $primaryFocus;
    }
  }
}


.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  input{
    &:hover~label{
      &:before{
        border-color: $primary;
      }
    }
    &:focus~label{
      &:before{
        border-color: $primary;
        box-shadow: 0 0 0 2px $primaryFocus;
      }
    }
  }
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2em;
    min-height: 1.625em;
    color: $grey90;
    font-weight: 500;
    span{
      font-size: 1.125em;
      line-height: 145%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
      background-color: #fff;
      border: 1px solid $grey50;
      width: 1.25em;
      height: 1.25em;
    }
    a{
      position: relative;
      z-index: 2;
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked{
      &+label {
        &::before {
          background-color: $primary;
          border-color: $primary;
          background-size: 100% auto;
        }
      }
      &:hover~label{
        &:before{
          background-color: $primaryHover;
        }
      }
      &:focus~label{
        &:before{
          background-color: $primaryPressed;
        }
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      @include bgImgSize(0);
      background-image: url(../img/check.svg);
      transition: 0s;
    }
  }
}
