.footer{
  margin-top: 8.75em;
  padding: 2.5em 0;
  background-color: $primaryPressed;
  color: #FFF;
  &__inner{
    margin-top: 3.75em;
    display: flex;
    justify-content: space-between;
  }
  &__info{
    max-width: 18.1875em;
  }
  &__eye{
    margin-top: 5em;
    width: 100%;
  }
  &__c{
    margin-top: 3.75em;
    text-align: center;
  }
}

.help{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2em;
  border-radius: 2em;
  border: 1px solid $primaryBorder;
  &__info{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 4.6875em;
    flex-shrink: 0;
    margin-right: 2.875em;
  }
}

.footer-group{
  width: 14em;
  max-width: 100%;
  &__top{
    padding: .25em 0;
    text-transform: uppercase;
    border-bottom: 1px solid $grey70;
  }
  &__bottom{
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links,
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .38em;
    &._active{
      .footer-group__show{
        border-color: #fff;
        svg{
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__link{
    margin-top: .38em;
    color: #fff;
  }
  &__show,
  &__link{
    span{
      font-size: 1.25em;
      line-height: 140%;
    }
    &:hover{
      color: $primary;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    border-bottom: 2px solid transparent;
    svg{
      width: 1em;
      height: auto;
      margin-left: .5em;
    }
    &:hover{
      cursor: pointer;
      ._fill{
        fill: $primary;
      }
      ._stroke{
        stroke: $primary;
      }
    }
  }
  &__hidden{
    display: none;
    padding-left: 1em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 6em;
    &__info{
      display: none;
    }
  }

  .help{
    display: block;
    &__img{
      margin-right: 1em;
    }
    &__btn{
      width: 100%;
      margin: 2.5em 0 0;
    }
  }

  .footer-group{
    width: 13.5em;
  }
}


@media screen and (max-width: 576px){
  .help{
    &__img{
      width: 2.875em;
      margin-right: .75em;
    }
    &__btn{
      width: 100%;
      margin: 2.5em 0 0;
    }
  }

  .footer{
    &__inner{
      @include grid100gap(1em);
    }
  }

  .footer-group{
    width: 100%;
    &._active{
      .footer-group__top{
        color: $primary;
        &:after{
          transform: rotate(-180deg);
        }
      }
    }
    &__top{
      border-color: #FFF;
      padding: .25em 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      text-transform: unset;
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/arrow-down.svg);
        @include bgImgSize(100%);
        transition: all .4s;
      }
      .text16{
        font-size: 1.75em;
        line-height: 129%;
        font-weight: 600;
      }
    }
    &__bottom{
      display: none;
    }
  }
}
