.header{
  z-index: 10;
  padding: 1.75em 0;
  position: relative;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    position: relative;
    z-index: 2;
    display: block;
    width: 2.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  .global-search{
    display: none;
  }
  &._open-additional-menu{
    .additional-menu{
      opacity: 1;
      pointer-events: unset;
    }
    ._show-additional-menu{
      color: $primary;
      border-bottom-color: $primary;
      svg{
        transform: rotate(-180deg);
      }
      ._fill{
        fill: $primary
      }
      ._stroke{
        stroke: $primary
      }
    }
  }
}


.header-action{
  position: relative;
  z-index: 2;
  &__content{
    display: flex;
    align-items: center;
  }
  &__top{
    display: none;
  }
  &__btn{
    @include lastM(0 1.5em 0 0);
    ._view2{
      display: none;
    }
  }
  &__eye{
    display: flex;
    align-items: center;
    svg{
      width: 2em;
      margin: 0;
      height: auto;
    }
    span{
      display: none;
    }
    &:hover{
      opacity: .8;
    }
  }
}

.header-user-btn,
.burger{
  display: none;
}

.header-item{
  padding: .25em 0;
  @include grid100gap(.5em);
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon,
  &__arrow{
    width: 2em;
    flex-shrink: 0;
  }
  &__icon{
    margin-right: .625em;
  }
  &__arrow{
    margin-left: auto;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.avatar{
  width: 2.625em;
  height: 2.625em;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 50%;
  }
}

.header-user{
  position: relative;
  margin-right: 2.5em;
  &__toggle{
    display: flex;
    align-items: center;
    color: $primary;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      color: $primaryHover;
      ._stroke-path{
        stroke: $primaryHover;
      }
      ._fill-path{
        fill: $primaryHover;
      }
    }

  }
  .avatar{
    margin-right: .5em;
  }
  &__arrow{
    width: 1em;
    height: auto;
    margin-left: .5em;
  }
  &__desc{
    display: none;
  }
  &__dropdown{
    @media screen and (min-width: 993px){
      position: absolute;
      top: 100%;
      left: 0;
      width: calc(100% + 1.62em);
      box-shadow: 0 .5em 1em 0 rgba(0, 0, 0, 0.08);
      padding: 1.875em 1em;
      border-radius: .75em;
      background-color: $bg;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      .header-user__logout-text,
      .header-item__arrow{
        display: none;
      }
      .header-item__top{
        color: $primary;
        &:hover{
          color: $primaryHover;
        }
      }
    }
  }
  &._active{
    color: $primaryPressed;
    .header-user__arrow{
      transform: rotate(-180deg);
    }
    ._stroke-path{
      stroke: $primaryPressed;
    }
    ._fill-path{
      fill: $primaryPressed;
    }
    .header-user__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.menu{
  &__top{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link,
  &__item{
    @include lastM(0 1.875em 0 0);
  }
  &__link,
  &__show{
    transition: all .3s;
    position: relative;
    z-index: 2;
    &:not(:hover){
      color: $grey90;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    &:hover{

    }
  }
  &__hidden{
    @media screen and (min-width: 993px){
      display: block !important;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%,0);
      opacity: 0;
      pointer-events: none;
      transition: all .4s;
      max-width: 100%;
      width: 1224px;
      padding: 6.12em 2em 2.5em;
      border-radius: 0 0 2em 2em;
      background: $bg;
      box-shadow: 0 .5em 1em 0 rgba(0, 0, 0, 0.08);
    }
  }
}

.menu-group{
  @include lastM(0 0 1.875em);
  &__top{
    padding: .5em 0;
    text-transform: uppercase;
    color: $primaryPressed;
    border-bottom: 1px solid $primaryPressed;
  }
  &__items{
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .75em 0;
    background-color: #FEFEFE;
    box-shadow: 0 .125em .25em 0 rgba(0, 0, 0, 0.12);
    &__logo{
      width: 1.6em;
      z-index: unset;
    }
    .global-search{
      display: block;
      margin-bottom: 1.5em;
    }
    &._open-user-menu{
      .header-action{
        transform: translateX(0);
      }
    }
    &._open-burger-menu{
      .menu{
        transform: translateX(0);
      }
    }
  }

  .header-action{
    @include menu;
    &__content{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      @include lastM(0 0 1.5em);
      width: 100%;
      padding-left: .72em;
      padding-right: .72em;
      ._view1{
        display: none;
      }
      ._view2{
        display: grid;
      }
    }
    &__eye{
      color: $grey90;
      border-radius: 0;
      padding: .5em 0;
      border-bottom: 1px solid $primary;
      svg{
        display: none;
      }
      span{
        display: block;
      }
    }
  }

  .header-user-btn,
  .burger{
    display: flex;
  }

  .header-user{
    margin: 0 0 1.5em;
    width: 100%;
    &__toggle{
      color: $grey100;
      padding: .75em 0;
    }
    &__text{
      @include grid100gap(.25em);
    }
    &__desc{
      display: block;
    }
    &__arrow{
      display: none;
    }
    &__dropdown{
      margin-top: 3.75em;
    }
  }

  .menu{
    @include menu;
    //transform: translateX(0);
    &__content{
      padding: 1.5em 1em;
    }
  }

  .nav{
    @include grid100gap(1.5em);
    &__link,
    &__item{
      margin: 0 !important;
    }
    &__link,
    &__show{
      width: 100%;
      height: unset;
      padding: .5em 0;
      justify-content: unset;
      border-bottom: 1px solid $primary;
      border-top: none;
      span{
        font-weight: 600;
        font-size: 1.75em;
        line-height: 129%;
      }
    }
    &__show{
      justify-content: space-between;
      svg{
        width: 1em;
      }
      ._fill{
        fill: $primary
      }
      ._stroke{
        stroke: $primary
      }
    }
    &__hidden{
      padding: 1.5em 0;
      display: none;
    }
  }

  .menu-group{
    &__items{
      margin-top: 1.5em;
    }
  }
}
